:root {
  --dropdown-label-color: #0fc;
  --dropdown-text-color: #0fc;
  --button-color: #0fc;
  --title-color: #0fc;
  --drop-off-header-text-color: #0fc;
  --table-header-text-color: #0fc;
  --pickup-text-color: #0fc;
  --commodity-text-color: #0fc;
  --amount-text-color: #0fc;
  --row-text-color: #0fc;
  --mission-text-color: #0fc;
  --table-outline-color: #0fc;
}

body {
  color: #0fc;
  background-color: #0d0d0d;
  width: 1200px;
  height: 800px;
  margin: 0 auto;
  padding: 0;
  font-family: Orbitron, sans-serif;
  transition: background-color .3s, color .3s;
}

header {
  background-color: #1a1a1a;
  border-bottom: 1px solid #333;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

header h1 {
  color: var(--title-color);
  margin: 0;
  font-size: 24px;
}

button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

button:hover {
  background-color: #0c9;
}

.add-entry-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  text-align: center;
  white-space: nowrap;
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: 7px 20px;
  font-family: inherit;
  font-size: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: inline-block;
}

.add-entry-button:hover {
  color: #0d0d0d;
  background-color: #0c9;
}

.add-entry-button:disabled {
  color: #999;
  cursor: not-allowed;
  background-color: #666;
}

.add-entry-button:active {
  transform: scale(.98);
}

.clear-log-button {
  background-color: #f66;
  width: 150px;
  height: 35px;
}

.clear-log-button:hover {
  background-color: #f33;
}

.process-orders-button {
  background-color: var(--button-color);
  flex-wrap: nowrap;
  width: 180px;
  height: 35px;
}

.process-orders-button:hover {
  background-color: #09c;
}

.scu-group {
  flex-direction: row;
  align-items: center;
  gap: 5px;
  display: flex;
}

.scu-label {
  color: #0fc;
  text-wrap: wrap;
  white-space: pre-wrap;
  align-items: center;
  margin-left: 10px;
  font-size: 16px;
}

.scu-total {
  color: #0fc;
  align-items: center;
  font-size: 14px;
}

.scu-input {
  color: #0fc;
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  align-items: center;
  width: 40px;
  margin-top: 10px;
  padding: 10px;
}

.amount-input {
  color: #0fc;
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  width: 30px;
  height: 20px;
  padding: 10px;
}

.dark-mode {
  color: #0fc;
  background-color: #0d0d0d;
}

.main-tabs {
  background-color: #1a1a1a;
  border-bottom: 1px solid #333;
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.main-tab {
  cursor: pointer;
  color: #0fc;
  padding: 10px 20px;
  transition: color .3s, border-bottom .3s;
}

.main-tab:hover {
  color: #0c9;
}

.active-main-tab {
  border-bottom: 2px solid #0fc;
  font-weight: bold;
}

.tabs {
  background-color: #1a1a1a;
  border-bottom: 1px solid #333;
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.tab {
  cursor: pointer;
  color: #0fc;
  padding: 10px 20px;
  transition: color .3s, border-bottom .3s;
}

.tab:hover {
  color: #0c9;
}

.active-tab {
  border-bottom: 2px solid #0fc;
  font-weight: bold;
}

.content {
  background-color: #1a1a1a;
  border-top: 1px solid #333;
  padding: 20px;
  box-shadow: 0 -4px 8px #0003;
}

h2 {
  color: #0fc;
}

p {
  color: #ccc;
}

.hauling-missions {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.form-group {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.form-group label:not(.preferences label) {
  color: var(--dropdown-label-color);
  text-align: center;
}

.form-group label[for="location-type"] {
  white-space: nowrap;
}

select.location-type {
  width: 85px;
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  align-self: center;
  padding: 10px;
  font-family: Orbitron, sans-serif;
}

select.location-type option {
  white-space: nowrap;
}

select.location, select.moon {
  width: 110px;
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 10px;
  font-family: Orbitron, sans-serif;
}

select.drop-off {
  width: 300px;
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 10px;
  font-family: Orbitron, sans-serif;
}

select.commodity {
  width: 120px;
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 10px;
  font-family: Orbitron, sans-serif;
}

select.first-dropdown, select.second-dropdown, select.planet {
  width: 110px;
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 10px;
  font-family: Orbitron, sans-serif;
}

.selectable-option {
  padding-right: 10px;
}

input.first-dropdown, input.second-dropdown {
  width: 110px;
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  margin-bottom: 5px;
  padding: 10px;
  font-family: Orbitron, sans-serif;
}

.form-row {
  align-items: center;
  gap: 10px;
  display: flex;
}

.button-group {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-flow: wrap;
  align-items: center;
  gap: 10px;
  width: 750px;
  margin-top: 20px;
  margin-left: 10px;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.actions {
  gap: 10px;
  display: flex;
}

.actions button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

.actions button:hover {
  background-color: #0c9;
}

.actions .form-group {
  flex-direction: row;
  align-items: center;
  gap: 5px;
}

.actions .form-group label {
  margin: 0;
}

.actions .form-group input {
  color: #0fc;
  background-color: #333;
  width: 100px;
}

.scu-container {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.scu-label {
  color: #0fc;
  white-space: pre-wrap;
  margin-left: 10px;
  font-size: 15px;
}

.scu-total {
  color: #0fc;
  font-size: 14px;
}

.table-container {
  border-radius: 10px;
  margin-top: 20px;
  overflow-x: auto;
}

.hauling-manifest-table {
  border-collapse: separate;
  border: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  border-radius: 10px;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 4px 8px #0003;
}

.hauling-manifest-table th, .hauling-manifest-table td {
  text-align: left;
  border: 1px solid var(--table-outline-color);
  padding: 10px;
}

.hauling-manifest-table th.pickup, .hauling-manifest-table td.pickup {
  text-align: center;
  width: 370px;
  color: var(--pickup-text-color);
  padding: 5px;
  font-size: 14px;
}

.hauling-manifest-table th.commodity, .hauling-manifest-table td.commodity {
  text-align: center;
  color: var(--commodity-text-color);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 160px !important;
}

.hauling-manifest-table th.amount, .hauling-manifest-table td.amount {
  text-align: center;
  width: 80px;
  color: var(--amount-text-color);
  font-size: 14px;
}

.hauling-manifest-table th.actions, .hauling-manifest-table td.actions {
  text-align: center;
}

.hauling-manifest-table th.status, .hauling-manifest-table td.status {
  text-align: center;
  width: 90px;
}

.hauling-manifest-table td:not(.status):not(.actions) {
  color: var(--row-text-color);
}

.hauling-manifest-table th, .hauling-manifest-table th.pickup, .hauling-manifest-table th.commodity, .hauling-manifest-table th.amount {
  color: var(--table-header-text-color);
}

.hauling-manifest-table td.pickup, .hauling-manifest-table td.commodity, .hauling-manifest-table td.amount {
  color: var(--row-text-color);
}

.hauling-manifest-table .actions button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 13px;
  transition: background-color .3s, color .3s;
}

.hauling-manifest-table .actions button:hover {
  background-color: #0c9;
}

.hauling-manifest-table .actions .remove-cargo-button {
  color: #0d0d0d;
  background-color: #f66;
  font-size: 12px;
}

.hauling-manifest-table .actions .remove-cargo-button:hover {
  background-color: #f33;
}

.hauling-manifest-table .actions input[type="text"] {
  color: #0fc;
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  width: 40px;
  padding: 5px;
  font-family: Orbitron, sans-serif;
}

.drop-off-header {
  cursor: pointer;
  color: var(--drop-off-header-text-color);
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 20px;
  transition: background-color .3s, color .3s;
  display: flex;
}

.drop-off-header:hover {
  color: #0c9;
  background-color: #333;
}

.drop-off-header .left-box {
  align-items: center;
  gap: 10px;
  display: flex;
}

.drop-off-header .left-box button {
  color: #0d0d0d;
  cursor: pointer;
  background-color: #0fc;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  transition: background-color .3s, color .3s;
}

.drop-off-header .left-box button:hover {
  background-color: #0c9;
}

.drop-off-header .right-box {
  align-items: center;
  gap: 10px;
  display: flex;
}

.drop-off-header .right-box span {
  color: #0fc;
  background-color: #333;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
}

.drop-off-header .right-box button {
  color: #0d0d0d;
  cursor: pointer;
  background-color: #0fc;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  transition: background-color .3s, color .3s;
}

.drop-off-header .right-box button:hover {
  background-color: #0c9;
}

.drop-off-header .right-box .ocr-mission-payout-capture {
  max-width: 120px;
  color: var(--dropdown-text-color);
  text-align: left;
  background-color: #333;
  border: 1px solid #555;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 14px;
}

.drop-off-header .right-box .ocr-mission-payout-capture:focus {
  border-color: var(--button-color);
  box-shadow: 0 0 0 1px var(--button-color);
  outline: none;
}

.drop-off-header .right-box .ocr-mission-payout-capture::placeholder {
  color: #666;
}

.mission-checkbox {
  border: 1px solid #0fc;
  border-radius: 5px;
  flex-wrap: wrap;
  gap: 5px;
  width: 210px;
  margin-top: -50px;
  margin-bottom: -20px;
  margin-left: 70px;
  padding: 10px;
  display: flex;
}

.mission-checkbox .column {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.mission-checkbox label {
  color: var(--mission-text-color);
  font-size: 14px;
  position: relative;
}

.mission-checkbox label:hover .tooltip {
  display: block;
}

.tooltip {
  color: #0fc;
  z-index: 10;
  background-color: #1a1a1a;
  border: 1px solid #0fc;
  border-radius: 5px;
  width: 700px;
  min-height: 50px;
  max-height: 500px;
  padding: 10px;
  transition: height .3s;
  display: none;
  position: absolute;
  top: 0;
  right: 100%;
  overflow: auto;
}

.react-select__control {
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 5px;
  font-family: Orbitron, sans-serif;
  font-size: 14px;
}

.react-select__menu, .react-select__option {
  color: var(--dropdown-text-color);
  background-color: #333;
}

.react-select__option--is-focused {
  background-color: #444;
}

.react-select__option--is-selected {
  background-color: #555;
}

.react-select__single-value {
  color: var(--dropdown-text-color);
}

.first-dropdown-select .react-select__control, .second-dropdown-select .react-select__control {
  width: 350px;
}

.location-type-select .react-select__control {
  width: 90px;
}

.planet-select .react-select__control {
  width: 119px;
}

.moon-select .react-select__control {
  width: 110px;
}

.drop-off-select .react-select__control {
  width: 300px;
}

.commodity-select .react-select__control {
  width: 154px;
}

.station-select .react-select__control {
  width: 119px;
}

.react-select__menu-list {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.react-select__menu-list::-webkit-scrollbar {
  display: none;
}

.react-select__input input {
  color: #fff;
}

.preferences {
  color: #0fc;
  text-align: left;
}

.preferences .form-group {
  align-items: flex-start;
}

.preferences input[type="color"] {
  margin-left: 10px;
}

.preferences-box {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  width: 250px;
  margin-bottom: 20px;
  padding: 20px;
  transition: background-color .3s, border-color .3s;
  box-shadow: 0 4px 8px #0003;
}

.preferences-box:hover {
  background-color: #333;
  border-color: #0fc;
}

.preferences-box h3 {
  border-bottom: 2px solid #0fc;
  padding-bottom: 5px;
}

.preferences-container {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-direction: row;
  gap: 20px;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.banner {
  color: #0d0d0d;
  text-align: center;
  z-index: 1000;
  background-color: #f66;
  padding: 10px;
  font-size: 16px;
  animation: .3s ease-out slideDown;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 4px 8px #0003;
}

@keyframes slideDown {
  from {
    transform: translateY(-100%);
  }

  to {
    transform: translateY(0);
  }
}

.location-box {
  background-color: #2a2a2a;
  border: 1px solid #444;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 20px;
  transition: background-color .3s, border-color .3s;
  box-shadow: 0 4px 8px #0003;
}

.location-box:hover {
  background-color: #444;
  border-color: #0c9;
}

.cargo-manifest-box {
  background-color: #2a2a2a;
  border: 1px solid #444;
  border-radius: 10px;
  width: 280px;
  margin-bottom: 20px;
  padding: 20px;
  transition: background-color .3s, border-color .3s;
  box-shadow: 0 4px 8px #0003;
}

.cargo-manifest-box:hover {
  background-color: #444;
  border-color: #0c9;
}

.history-container {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px #0003;
}

.history-group-box {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.history-date-group {
  border: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  border-radius: 10px;
  overflow: hidden;
}

.history-date-header {
  color: var(--drop-off-header-text-color);
  cursor: pointer;
  background-color: #333;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .3s;
}

.history-date-header:hover {
  background-color: #444;
}

.drop-off-group-history {
  background-color: #1a1a1a;
  border-radius: 5px;
  flex-direction: column;
  gap: 2px;
  padding: 5px;
  display: flex;
}

.drop-off-header-history-header {
  cursor: pointer;
  background-color: #333;
  border: 1px solid #000;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 22px;
  transition: background-color .3s;
  display: flex;
}

.drop-off-header-history-header:hover {
  background-color: #333;
}

.commodity-section {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 10px;
}

.commodity-header {
  color: var(--table-header-text-color);
  cursor: pointer;
  background-color: #333;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
  transition: background-color .3s;
  display: flex;
}

.commodity-header:hover {
  background-color: #444;
}

.history-table {
  border-collapse: separate;
  border: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  border-radius: 5px;
  width: 100%;
  overflow: hidden;
}

.history-table th, .history-table td {
  text-align: left;
  border: 1px solid var(--table-outline-color);
  padding: 6px;
}

.history-table th {
  color: var(--table-header-text-color);
  background-color: #333;
  padding: 8px;
  font-size: 14px;
  font-weight: bold;
}

.history-table td {
  color: var(--row-text-color);
  padding: 4px;
  font-size: 12px;
}

.history-table tr:hover {
  background-color: #333;
}

@media (width <= 768px) {
  .history-date-header {
    padding: 10px;
    font-size: 16px;
  }

  .commodity-header {
    font-size: 14px;
  }

  .history-table th, .history-table td {
    padding: 8px;
    font-size: 14px;
  }
}

.preferences-box input[type="file"] {
  display: none;
}

.preferences-box label[for="file-upload"] {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: inline-block;
}

.preferences-box label[for="file-upload"]:hover {
  background-color: #0c9;
}

.preferences-box button.export-data-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  width: 180px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: inline-block;
}

.preferences-box button.export-data-button:hover {
  background-color: #0c9;
}

.preferences-box button.clear-history-log-button {
  color: #0d0d0d;
  cursor: pointer;
  background-color: #f66;
  border: none;
  border-radius: 5px;
  width: 180px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: inline-block;
}

.preferences-box button.clear-history-log-button:hover {
  background-color: #f33;
}

@media (width <= 1090px) {
  body {
    transform-origin: 0 0;
    transition: transform .3s;
    transform: scale(1);
  }
}

@media (width <= 1080px) {
  body {
    transform-origin: 0 0;
    transition: transform .3s;
    transform: scale(.99);
  }
}

@media (width <= 1070px) {
  body {
    transform-origin: 0 0;
    transition: transform .3s;
    transform: scale(.98);
